import { McpAgent } from "agents/mcp";
import { McpServer } from "@modelcontextprotocol/sdk/server/mcp.js";
import { z } from "zod";
import { JsonToSqlDO } from "./do.js";

// ========================================
// API CONFIGURATION - Customized for RCSB PDB
// ========================================
const API_CONFIG = {
	name: "RcsbPdbExplorer",
	version: "0.1.0",
	description: `MCP Server for querying the RCSB Protein Data Bank (PDB) GraphQL API and converting responses to queryable SQLite tables.

Before running any specific data queries, it is **strongly recommended** to use GraphQL introspection queries to explore and understand the schema of the RCSB PDB API. Introspection allows you to:

- **Discover all available types, fields, and relationships** in the API, ensuring you know exactly what data you can access and how to structure your queries.
- **Avoid common errors** due to typos or incorrect field names, as you can verify the schema directly before querying.
- **Stay resilient to schema changes**: The RCSB PDB API may evolve, and introspection lets you dynamically adapt to new or deprecated fields.
- **Craft more efficient and precise queries** by understanding which fields are available and how they are nested, reducing unnecessary trial and error.
- **Accelerate development and debugging**: Introspection provides a live, up-to-date contract for the API, making it easier to troubleshoot and optimize your queries.

The server provides two main tools:
1. **GraphQL Query Tool**: Executes queries against the RCSB PDB API and stages results in SQLite tables
2. **SQL Query Tool**: Allows analytical queries against the staged data using full SQL capabilities`,
	
	// GraphQL API settings
	endpoint: 'https://data.rcsb.org/graphql',
	headers: {
		"Accept": "application/json",
		"User-Agent": "RcsbPdbMCP/0.1.0 (ModelContextProtocol; +https://modelcontextprotocol.io)"
	},
	
	// Tool names and descriptions
	tools: {
		graphql: {
			name: "rcsb_pdb_graphql_query",
			description: `Executes a GraphQL query against the RCSB PDB Data API (https://data.rcsb.org/graphql), processes responses into SQLite tables, and returns metadata for subsequent SQL querying.

**Tip:** For best results, start by using GraphQL introspection queries to explore the schema before running other queries. Introspection helps you discover all available types, fields, and relationships, prevents errors, and ensures your queries are accurate and up-to-date.

**Why use introspection?**
- See exactly what data is available and how to access it.
- Avoid errors from incorrect field names or outdated assumptions.
- Adapt quickly to schema changes.
- Write more efficient, targeted queries.

**Example introspection query:**
\`\`\`
{ __schema { types { name kind fields { name } } } }
\`\`\`

After exploring the schema, you can query for specific entries, polymer entities, assemblies, chemical components, etc.

**Example data queries:**
- Experimental method for PDB entry 4HHB:
  \`{ entry(entry_id:"4HHB") { exptl { method } } }\`
- Details for multiple entries:
  \`{ entries(entry_ids: ["4HHB", "12CA"]) { rcsb_id struct { title } } }\`
- Taxonomy for polymer entities (e.g., 4HHB_1 where 1 is entity_id):
  \`{ polymer_entity(entry_id: "4HHB", entity_id:"1") { rcsb_entity_source_organism { ncbi_scientific_name } } }\`

Returns a data_access_id and schema information for use with the SQL querying tool.`
		},
		sql: {
			name: "rcsb_pdb_query_sql", 
			description: "Execute read-only SQL queries against staged RCSB PDB data. Use the data_access_id from rcsb_pdb_graphql_query to query the SQLite tables. Supports analytical queries, CTEs, temporary tables, and JSON functions for analyzing protein structure data."
		}
	}
};

// In-memory registry of staged datasets
const datasetRegistry = new Map<string, { created: string; table_count?: number; total_rows?: number }>();

// ========================================
// ENVIRONMENT INTERFACE
// ========================================
interface RcsbPdbEnv extends Env {
	MCP_HOST?: string;
	MCP_PORT?: string;
	JSON_TO_SQL_DO: DurableObjectNamespace;
}

// ========================================
// CORE MCP SERVER CLASS - Enhanced for RCSB PDB
// ========================================

export class RcsbPdbMCP extends McpAgent {
	server = new McpServer({
		name: API_CONFIG.name,
		version: API_CONFIG.version,
		description: API_CONFIG.description,
		capabilities: {
			tools: {}, // Indicates tool support
		}
	});

	async _init(props: any) {
		await this.init();
	}

	async init() {
		console.error("RCSB PDB MCP Server initialized with enhanced SQLite staging capabilities.");

		// Tool #1: GraphQL to SQLite staging
		this.server.tool(
			API_CONFIG.tools.graphql.name,
			API_CONFIG.tools.graphql.description,
			{
				query: z.string().describe(
					`The GraphQL query string to execute against the RCSB PDB GraphQL API.

**Pro tip:** Use introspection queries like '{ __schema { types { name kind fields { name } } } }' to discover the schema before running other queries. This helps you avoid errors and ensures your queries are valid.

Example data query: '{ entry(entry_id:"4HHB") { struct { title } exptl { method } } }'.`
				),
				variables: z
					.record(z.any())
					.optional()
					.describe(
						"Optional dictionary of variables for the GraphQL query. Example: { \"entry_id\": \"4HHB\" }"
					),
			},
			async ({ query, variables }) => {
				try {
					const graphqlResult = await this.executeGraphQLQuery(query, variables);

					if (this.shouldBypassStaging(graphqlResult, query)) {
						return {
							content: [{
								type: "text" as const,
								text: JSON.stringify(graphqlResult, null, 2)
							}]
						};
					}

					const stagingResult = await this.stageDataInDurableObject(graphqlResult);
					return {
						content: [{
							type: "text" as const,
							text: JSON.stringify(stagingResult, null, 2)
						}]
					};

				} catch (error) {
					return this.createErrorResponse("GraphQL execution failed", error);
				}
			}
		);

		// Tool #2: SQL querying against staged data
		this.server.tool(
			API_CONFIG.tools.sql.name,
			API_CONFIG.tools.sql.description,
			{
				data_access_id: z.string().describe("Data access ID from the GraphQL query tool"),
				sql: z.string().describe("SQL SELECT query to execute against the staged PDB data"),
			},
			async ({ data_access_id, sql }) => {
				try {
					const queryResult = await this.executeSQLQuery(data_access_id, sql);
					return { content: [{ type: "text" as const, text: JSON.stringify(queryResult, null, 2) }] };
				} catch (error) {
					return this.createErrorResponse("SQL execution failed", error);
				}
			}
		);
	}

	// ========================================
	// JSON-TO-SQL FUNCTIONALITY (Integrated from JsonToSqlDO)
	// ========================================
	
	async processAndStoreJson(jsonData: any): Promise<any> {
		try {
			let dataToProcess = jsonData?.data ? jsonData.data : jsonData;
			const { PaginationAnalyzer } = await import('./lib/PaginationAnalyzer.js');
			const paginationInfo = PaginationAnalyzer.extractInfo(dataToProcess);

			const { SchemaInferenceEngine } = await import('./lib/SchemaInferenceEngine.js');
			const schemaEngine = new SchemaInferenceEngine();
			const schemas = schemaEngine.inferFromJSON(dataToProcess);
			
			// Create tables
			await this.createTables(schemas);
			
			// Insert data
			const { DataInsertionEngine } = await import('./lib/DataInsertionEngine.js');
			const dataInsertionEngine = new DataInsertionEngine();
			await dataInsertionEngine.insertData(dataToProcess, schemas, this.ctx.storage.sql);
			
			// Generate metadata
			const metadata = await this.generateMetadata(schemas);
			
			// Add pagination if available
			if (paginationInfo.hasNextPage) {
				metadata.pagination = paginationInfo;
			}
			
			return {
				success: true,
				message: "Data processed successfully",
				...metadata
			};
			
		} catch (error) {
			return {
				success: false,
				message: error instanceof Error ? error.message : "Processing failed"
			};
		}
	}

	async executeSqlQuery(sqlQuery: string): Promise<any> {
		try {
			// Enhanced security validation for analytical SQL
			const validationResult = this.validateAnalyticalSql(sqlQuery);
			if (!validationResult.isValid) {
				throw new Error(validationResult.error);
			}

			const result = this.ctx.storage.sql.exec(sqlQuery);
			const results = result.toArray();

			return {
				success: true,
				results,
				row_count: results.length,
				column_names: result.columnNames || [],
				query_type: validationResult.queryType
			};

		} catch (error) {
			return {
				success: false,
				error: error instanceof Error ? error.message : "SQL execution failed",
				query: sqlQuery
			};
		}
	}

	private validateAnalyticalSql(sql: string): {isValid: boolean, error?: string, queryType?: string} {
		const trimmedSql = sql.trim().toLowerCase();
		
		// Allowed operations for analytical work
		const allowedStarters = [
			'select',
			'with',           // CTEs for complex analysis
			'pragma',         // Schema inspection
			'explain',        // Query planning
			'create temporary table',
			'create temp table',
			'create view',
			'create temporary view',
			'create temp view',
			'drop view',      // Clean up session views
			'drop temporary table',
			'drop temp table'
		];

		// Dangerous operations that modify permanent data
		const blockedPatterns = [
			/\bdrop\s+table\s+(?!temp|temporary)/i,    // Block permanent table drops
			/\bdelete\s+from/i,                        // Block data deletion
			/\bupdate\s+\w+\s+set/i,                   // Block data updates
			/\binsert\s+into\s+(?!temp|temporary)/i,   // Block permanent inserts
			/\balter\s+table/i,                        // Block schema changes
			/\bcreate\s+table\s+(?!temp|temporary)/i,  // Block permanent table creation
			/\battach\s+database/i,                    // Block external database access
			/\bdetach\s+database/i                     // Block database detachment
		];

		// Check if query starts with allowed operation
		const startsWithAllowed = allowedStarters.some(starter => 
			trimmedSql.startsWith(starter)
		);

		if (!startsWithAllowed) {
			return {
				isValid: false, 
				error: `Query type not allowed. Permitted operations: ${allowedStarters.join(', ')}`
			};
		}

		// Check for blocked patterns
		for (const pattern of blockedPatterns) {
			if (pattern.test(sql)) {
				return {
					isValid: false,
					error: `Operation blocked for security: ${pattern.source}`
				};
			}
		}

		// Determine query type for response metadata
		let queryType = 'select';
		if (trimmedSql.startsWith('with')) queryType = 'cte';
		else if (trimmedSql.startsWith('pragma')) queryType = 'pragma';
		else if (trimmedSql.startsWith('explain')) queryType = 'explain';
		else if (trimmedSql.includes('create')) queryType = 'create_temp';

		return {isValid: true, queryType};
	}

	private async createTables(schemas: Record<string, any>): Promise<void> {
		for (const [tableName, schema] of Object.entries(schemas)) {
			try {
				// Validate table name
				const validTableName = this.validateAndFixIdentifier(tableName, 'table');
				
				// Validate and fix column definitions
				const validColumnDefs: string[] = [];
				for (const [name, type] of Object.entries(schema.columns)) {
					const validColumnName = this.validateAndFixIdentifier(name, 'column');
					const validType = this.validateSQLiteType(type as string);
					validColumnDefs.push(`${validColumnName} ${validType}`);
				}

				if (validColumnDefs.length === 0) {
					console.warn(`Skipping table ${tableName} - no valid columns`);
					continue;
				}

				const createTableSQL = `CREATE TABLE IF NOT EXISTS ${validTableName} (${validColumnDefs.join(', ')})`;
				
				// Add logging for debugging
				console.log(`Creating table with SQL: ${createTableSQL}`);
				
				this.ctx.storage.sql.exec(createTableSQL);
			} catch (error) {
				console.error(`Error creating table ${tableName}:`, error);
				// Try to create a fallback table with safe defaults
				try {
					const fallbackTableName = this.validateAndFixIdentifier(tableName, 'table');
					const fallbackSQL = `CREATE TABLE IF NOT EXISTS ${fallbackTableName} (id INTEGER PRIMARY KEY AUTOINCREMENT, data_json TEXT)`;
					this.ctx.storage.sql.exec(fallbackSQL);
				} catch (fallbackError) {
					console.error(`Failed to create fallback table for ${tableName}:`, fallbackError);
					// Skip this table entirely
				}
			}
		}
	}

	private validateAndFixIdentifier(name: string, type: 'table' | 'column'): string {
		if (!name || typeof name !== 'string') {
			return type === 'table' ? 'fallback_table' : 'fallback_column';
		}

		// Remove or replace problematic characters
		let fixed = name
			.replace(/[^a-zA-Z0-9_]/g, '_')
			.replace(/_{2,}/g, '_')
			.replace(/^_|_$/g, '');

		// Ensure it doesn't start with a number
		if (/^[0-9]/.test(fixed)) {
			fixed = (type === 'table' ? 'table_' : 'col_') + fixed;
		}

		// Ensure it's not empty
		if (!fixed || fixed.length === 0) {
			return type === 'table' ? 'fallback_table' : 'fallback_column';
		}

		// Ensure it's not too long (SQLite limit is 1000 chars, but let's be conservative)
		if (fixed.length > 100) {
			fixed = fixed.substring(0, 100);
		}

		return fixed;
	}

	private validateSQLiteType(type: string): string {
		const validTypes = ['TEXT', 'INTEGER', 'REAL', 'BLOB', 'NUMERIC'];
		const upperType = type.toUpperCase();
		
		if (validTypes.includes(upperType)) {
			return upperType;
		}
		
		// Map common types to SQLite types
		const typeMapping: Record<string, string> = {
			'STRING': 'TEXT',
			'VARCHAR': 'TEXT',
			'CHAR': 'TEXT',
			'CLOB': 'TEXT',
			'INT': 'INTEGER',
			'BIGINT': 'INTEGER',
			'SMALLINT': 'INTEGER',
			'TINYINT': 'INTEGER',
			'BOOLEAN': 'INTEGER',
			'BOOL': 'INTEGER',
			'FLOAT': 'REAL',
			'DOUBLE': 'REAL',
			'DECIMAL': 'NUMERIC',
			'DATETIME': 'TEXT',
			'DATE': 'TEXT',
			'TIME': 'TEXT',
			'TIMESTAMP': 'TEXT',
			'JSON': 'TEXT'
		};
		
		return typeMapping[upperType] || 'TEXT';
	}

	private async generateMetadata(schemas: Record<string, any>): Promise<any> {
		const metadata: any = {
			table_count: Object.keys(schemas).length,
			total_rows: 0,
			tables: {}
		};

		for (const tableName of Object.keys(schemas)) {
			try {
				const validTableName = this.validateAndFixIdentifier(tableName, 'table');
				const countResult = this.ctx.storage.sql.exec(`SELECT COUNT(*) as count FROM ${validTableName}`);
				const count = countResult.toArray()[0]?.count || 0;
				
				metadata.total_rows += count;
				metadata.tables[tableName] = {
					row_count: count,
					columns: Object.keys(schemas[tableName].columns || {})
				};
			} catch (error) {
				console.error(`Error getting metadata for table ${tableName}:`, error);
				metadata.tables[tableName] = {
					row_count: 0,
					columns: [],
					error: "Could not retrieve metadata"
				};
			}
		}

		return metadata;
	}

	// ========================================
	// GRAPHQL CLIENT - Customized for RCSB PDB
	// ========================================
	private async executeGraphQLQuery(query: string, variables?: Record<string, any>): Promise<any> {
		const headers = {
			"Content-Type": "application/json",
			...API_CONFIG.headers
		};
		
		const body = { query, ...(variables && { variables }) };
		
		console.error(`Making GraphQL request to: ${API_CONFIG.endpoint}`);
		console.error(`Query: ${query.slice(0, 150)}...`);
		if (variables) {
			console.error(`Variables: ${JSON.stringify(variables).slice(0, 100)}...`);
		}
		
		const response = await fetch(API_CONFIG.endpoint, {
			method: 'POST',
			headers,
			body: JSON.stringify(body),
		});
		
		console.error(`RCSB PDB API response status: ${response.status}`);
		
		if (!response.ok) {
			const errorText = await response.text();
			throw new Error(`HTTP ${response.status}: ${errorText}`);
		}
		
		return await response.json();
	}

	private isIntrospectionQuery(query: string): boolean {
		if (!query) return false;
		
		// Remove comments and normalize whitespace for analysis
		const normalizedQuery = query
			.replace(/\s*#.*$/gm, '') // Remove comments
			.replace(/\s+/g, ' ')     // Normalize whitespace
			.trim()
			.toLowerCase();
		
		// Check for common introspection patterns
		const introspectionPatterns = [
			'__schema',           // Schema introspection
			'__type',            // Type introspection
			'__typename',        // Typename introspection
			'introspectionquery', // Named introspection queries
			'getintrospectionquery'
		];
		
		return introspectionPatterns.some(pattern => 
			normalizedQuery.includes(pattern)
		);
	}

	private shouldBypassStaging(result: any, originalQuery?: string): boolean {
		if (!result) return true;

		// Bypass if this was an introspection query
		if (originalQuery && this.isIntrospectionQuery(originalQuery)) {
			return true;
		}

		// Bypass if GraphQL reported errors
		if (result.errors) {
			return true;
		}

		// Check if response contains introspection-like data structure
		if (result.data) {
			// Common introspection response patterns
			if (result.data.__schema || result.data.__type) {
				return true;
			}
			
			// Check for schema metadata structures
			const hasSchemaMetadata = Object.values(result.data).some((value: any) => {
				if (value && typeof value === 'object') {
					// Look for typical schema introspection fields
					const keys = Object.keys(value);
					const schemaFields = ['types', 'queryType', 'mutationType', 'subscriptionType', 'directives'];
					const typeFields = ['name', 'kind', 'description', 'fields', 'interfaces', 'possibleTypes', 'enumValues', 'inputFields'];
					
					return schemaFields.some(field => keys.includes(field)) ||
						   typeFields.filter(field => keys.includes(field)).length >= 2;
				}
				return false;
			});
			
			if (hasSchemaMetadata) {
				return true;
			}
		}

		// Rough size check to avoid storing very small payloads
		try {
			if (JSON.stringify(result).length < 1500) {
				return true;
			}
		} catch {
			return true;
		}

		// Detect mostly empty data objects
		if (result.data) {
			const values = Object.values(result.data);
			const hasContent = values.some((v) => {
				if (v === null || v === undefined) return false;
				if (Array.isArray(v)) return v.length > 0;
				if (typeof v === "object") return Object.keys(v).length > 0;
				return true;
			});
			if (!hasContent) return true;
		}

		return false;
	}

	// ========================================
	// DURABLE OBJECT INTEGRATION
	// ========================================
	private async stageDataInDurableObject(graphqlResult: any): Promise<any> {
		const accessId = crypto.randomUUID();
		
		// Process data using integrated functionality
		const processingResult = await this.processAndStoreJson(graphqlResult);
		
		if (!processingResult.success) {
			throw new Error(`Data processing failed: ${processingResult.message}`);
		}
		
		datasetRegistry.set(accessId, {
			created: new Date().toISOString(),
			table_count: processingResult.table_count,
			total_rows: processingResult.total_rows
		});
		
		return {
			data_access_id: accessId,
			processing_details: processingResult
		};
	}

	private async executeSQLQuery(dataAccessId: string, sql: string): Promise<any> {
		// Check if dataset exists
		if (!datasetRegistry.has(dataAccessId)) {
			throw new Error(`Dataset ${dataAccessId} not found`);
		}
		
		// Execute SQL using integrated functionality
		return await this.executeSqlQuery(sql);
	}

	private async deleteDataset(dataAccessId: string): Promise<boolean> {
		const env = this.env as RcsbPdbEnv;
		if (!env?.JSON_TO_SQL_DO) {
			throw new Error("JSON_TO_SQL_DO binding not available");
		}

		const doId = env.JSON_TO_SQL_DO.idFromName(dataAccessId);
		const stub = env.JSON_TO_SQL_DO.get(doId);

		const response = await stub.fetch("http://do/delete", { method: 'DELETE' });

		return response.ok;
	}

	// ========================================
	// ERROR HANDLING
	// ========================================
	private createErrorResponse(message: string, error: unknown) {
		return {
			content: [{
				type: "text" as const,
				text: JSON.stringify({
					success: false,
					error: message,
					details: error instanceof Error ? error.message : String(error)
				}, null, 2)
			}]
		};
	}
}

// ========================================
// CLOUDFLARE WORKERS BOILERPLATE
// ========================================
interface Env {
	MCP_HOST?: string;
	MCP_PORT?: string;
	JSON_TO_SQL_DO: DurableObjectNamespace;
}

interface ExecutionContext {
	waitUntil(promise: Promise<any>): void;
	passThroughOnException(): void;
}

export default {
	async fetch(request: Request, env: Env, ctx: ExecutionContext): Promise<Response> {
		const url = new URL(request.url);

			if (url.pathname === "/sse" || url.pathname.startsWith("/sse/")) {
		// @ts-ignore - SSE transport handling
		return RcsbPdbMCP.serveSSE("/sse", { binding: "JSON_TO_SQL_DO" }).fetch(request, env, ctx);
	}

		if (url.pathname === "/datasets" && request.method === "GET") {
			const list = Array.from(datasetRegistry.entries()).map(([id, info]) => ({
				data_access_id: id,
				...info
			}));
			return new Response(JSON.stringify({ datasets: list }, null, 2), {
				headers: { "Content-Type": "application/json" }
			});
		}

		if (url.pathname.startsWith("/datasets/") && request.method === "DELETE") {
			const id = url.pathname.split("/")[2];
			if (!id || !datasetRegistry.has(id)) {
				return new Response(JSON.stringify({ error: "Dataset not found" }), {
					status: 404,
					headers: { "Content-Type": "application/json" }
				});
			}

			const doId = env.JSON_TO_SQL_DO.idFromName(id);
			const stub = env.JSON_TO_SQL_DO.get(doId);
			const resp = await stub.fetch("http://do/delete", { method: "DELETE" });
			if (resp.ok) {
				datasetRegistry.delete(id);
				return new Response(JSON.stringify({ success: true }), {
					headers: { "Content-Type": "application/json" }
				});
			}

			const text = await resp.text();
			return new Response(JSON.stringify({ success: false, error: text }), {
				status: 500,
				headers: { "Content-Type": "application/json" }
			});
		}

		return new Response(
			`${API_CONFIG.name} - Available on /sse endpoint for MCP protocol`,
			{ status: 404, headers: { "Content-Type": "text/plain" } }
		);
	},
};

export { RcsbPdbMCP as MyMCP };
export { JsonToSqlDO };
